 /* Color set "Watery blue-greens"
  * #021c1e - "blue black"
  * #004445 - "cadet blue"
  * #2c7873 - "rain"
  * #6fb98f - "greenery"
  */

.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow-x: hidden;
}

p {
    margin: 0;
    padding: 0;
}

.button {
    padding: 5px 7px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 12px 4px;
    height: 32px;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    cursor: pointer;
    background-color: #6fb98f;
    color: #004445;
    border: 2px solid #2c7873;
    border-radius: 5px;
}

.button:hover {
    background-color: #2c7873;
    color: white;
}

ul.navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #2c7873;
}

ul.navbar > li {
    height: 60px;
    background-color: #2c7873;
    vertical-align: middle;
}

li.nav-left {
    float: left;
}

li.nav-right {
    float: right;
}

ul.navbar > li > a, ul.navbar > li > span {
    display: block;
    padding: 20px;
    text-decoration: none;
    color: white;
}

ul.navbar > li > a:hover {
    background-color: #021c1e;
}

.screen {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    max-width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.screen h4 {
    width: 100%;
    margin: 10px;
    text-align: center;
}

.screen > div {
    padding-left: 10px;
    padding-right: 10px;
}

@media(min-width: 768px) {
    .screen > .half-screen {
        width: 50%;
    }

    .screen > .fifth-of-screen {
        width: 20%;
    }
}

.screen > div {
    -ms-flex-order: 2;
        order: 2;
}

@media(max-width: 768px) {
    .screen > div.pull-up-on-mobile {
        -ms-flex-order: 1;
            order: 1;
    }

    .button {
        padding: 3px 5px;
        margin: 16px 4px;
    }

    .half-screen {
        width: 100%;
    }
    .fifth-of-screen {
        width: 100%;
    }
}

.screen > div > p {
    padding: 0.5em;
    text-align: justify;
}

img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
}

ul.contacts {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    border: 1px solid #2c7873;
    border-radius: 5px;
}

a {
    color: #021c1e;
    text-decoration: none;
}

a:hover {
    color: #2c7873;
}

ul.contacts > li > a {
    display: block;
    padding: 8px 16px;
    text-align: center;
}

ul.contacts > li > a:hover {
    background-color: #6fb98f;
    color: white;
}

.iconed-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    width: 100%;
}

.iconed-list-item {
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #004445;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    overflow: auto;
}

.iconed-list-item-icon {
    background-color: #2c7873;
    color: white;
    font-size: 32px;
    min-width: 64px;
}

.iconed-list-item-icon-wide {
    background-color: #2c7873;
    color: white;
    font-size: 32px;
    min-width: 128px;
}

.iconed-list-item-icon > .fa, .iconed-list-item-icon > .fab, 
.iconed-list-item-icon > .fas, .iconed-list-item-icon > .far {
    text-align: center;
    padding: 25%;
}

.iconed-list-item-text {
    text-align: justify;
    padding: 15px;
    width: 100%;
}

@media(min-width: 768px) {
    .iconed-list-item {
        width: 33%;
    }
}

@media(max-width: 768px) {
    .iconed-list-item {
        width: 100%;
    }
}

table {
    border-collapse: collapse;
}

td {
    border-bottom: 1px solid #004445;
    padding: 10px;
}

table tr:last-child > td {
    border-bottom: none;
}

td > img {
    max-width: none;
}


